@media screen and (max-width: 720px) {
    .mainHeaderBox {
        height: 60px;
    }
    .mainHeader {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 60px;
        h1 {
            font-size: 0px;
            a {
                display: block;
                width: 100px;
                height: 5.639vw;
                background: url(../../../public/chemiverse_img/logo2.png) center
                    no-repeat;
                background-size: contain;
                margin-left: 5vw;
            }
        }
    }
    .searchBox {
        display: none;
    }
    .searchIcon {
        display: block;
        font-size: 30px;
        color: #b1b8c1;
    }
    .inner {
        .login {
            display: none;
        }
    }

    .mobile_right {
        display: flex;
    }
    input[id="menuicon"] {
        display: none;
    }
    input[id="menuicon"] + label {
        display: block;
        width: 30px;
        height: 30px;
        position: relative;
        cursor: pointer;
        margin-right: 5vw;
        z-index: 2;
    }
    input[id="menuicon"] + label span {
        display: block;
        position: absolute;
        width: 100%;
        height: 5px;
        border-radius: 30px;
        background-color: #b1b8c1;
        transition: all 0.35s;
    }
    input[id="menuicon"] + label span:nth-child(1) {
        top: 0;
    }
    input[id="menuicon"] + label span:nth-child(2) {
        top: 50%;
        transform: translateY(-50%);
    }
    input[id="menuicon"] + label span:nth-child(3) {
        bottom: 0;
    }
    input[id="menuicon"]:checked + label span:nth-child(1) {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
    }
    input[id="menuicon"]:checked + label span:nth-child(2) {
        opacity: 0;
    }
    input[id="menuicon"]:checked + label span:nth-child(3) {
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
    }

    .sidebar {
        display: block;
        width: 100vw;
        height: 100%;
        background-color: #fff;
        position: fixed;
        top: 60px;
        right: -100%;
        z-index: 1;
        transition: all 0.35s;
    }
    input[id="menuicon"]:checked + label + div {
        right: 0;
    }

    .navlist_white {
        color: #222;
        text-align: left;
        font-size: 6vw;
        height: 6vw;
        margin: 6vw auto;
        // border-bottom:1px solid #ccc ;
        font-weight: bold;
        padding-left: 5vw;
    }
}
