* {
  box-sizing: border-box;
}

.move .main {
  padding-left: 6.944vw;
  padding-right: 6.25vw;
  margin-bottom: 3.472vw;
  width: 100vw;
  height: 11.806vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-image: url(./../../../../public/chemiverse_img/bg-quiz-wrap.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 90%;
}
.move .mainTxt {
  display: flex;
  align-items: center;
}
.move .mainTxt span {
  display: inline-block;
  color: #fff;
}
.move .mainTxt span:first-child {
  font-size: 3.333vw;
  font-weight: 700;
  margin-right: 2.083vw;
}
.move .mainTxt span:last-child {
  font-size: 0.833vw;
  line-height: 1.5;
  font-weight: 500;
}
.move .mainImg {
  width: 15.833vw;
  height: auto;
}

.moveInner {
  width: 69.444vw;
  margin: auto;
}
.moveInner .platformInfo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: left;
  margin: 3rem 1rem;
}
.moveInner .platformLogo {
  width: 5.069vw;
  height: 4.306vw;
}
.moveInner .platformTitle {
  font-size: 2.778vw;
  text-align: center;
  margin-left: 1.5vw;
}

.itemboxs {
  width: 20.833vw;
  border: 0.069vw solid #bcbed3;
  border-radius: 0.694vw;
  margin: 1.036vw;
}
.itemboxs .move_class_img {
  width: 20.713vw;
  height: 18vw;
  border-radius: 0.694vw;
}
.itemboxs .itemtitle {
  text-align: left;
  font-size: 1.528vw;
  font-weight: bold;
}
.itemboxs .classname {
  font-size: 1.528vw;
  font-weight: bold;
  text-align: left;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 0.947vw 1.23vw;
}
.itemboxs .classinfotitle {
  align-items: center;
  text-align: left;
  padding: 0.147vw 1.23vw;
  font-size: 1.25vw;
}
.itemboxs .classinfo {
  text-align: center;
  padding: 0.147vw 0;
  font-size: 1.111vw;
  align-items: center;
}
.itemboxs .buttonarea {
  display: flex;
  justify-content: flex-end;
  padding: 0.25vw 1vw 1vw 2vw;
}
.itemboxs .copybutton {
  cursor: pointer;
  color: #6F76C1;
  margin-right: 1.139vw;
  margin-top: 0.439vw;
  align-items: center;
  font-size: 1.389vw;
}
.itemboxs .url {
  display: none;
}
.itemboxs .urlcopy {
  font-size: 0.833vw;
  align-items: center;
  text-align: center;
}
.itemboxs .button {
  width: 8.333vw;
  height: 2.431vw;
  border: 0.069vw solid #6F76C1;
  color: #6F76C1;
  text-align: center;
  align-items: center;
}

