::-webkit-scrollbar {
    display: none;
}
* {
    text-decoration: none;
    font-family: "Noto Sans KR", sans-serif;
}
::-webkit-scrollbar {
    display: none;
}
.App {
    text-align: center;
}
ul,
li {
    list-style: none;
}
ul {
    display: flex;
    justify-content: center;
}
* {
    box-sizing: border-box;
}
body,
button,
dd,
dl,
dt,
fieldset,
form,
h1,
h2,
h3,
h4,
h5,
h6,
input,
legend,
li,
ol,
p,
select,
table,
td,
textarea,
th,
ul {
    margin: 0;
    padding: 0;
}
body,
button,
input,
select,
table,
textarea {
    font-size: 12px;
    line-height: 16px;
    color: #202020;
    font-family: -apple-system, BlinkMacSystemFont, "Malgun Gothic", " ",
        helvetica, "Apple SD Gothic Neo", sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    line-height: inherit;
}
textarea {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    background-color: transparent;
    border: 0;
    word-break: keep-all;
    word-wrap: break-word;
}
button,
input {
    -webkit-border-radius: 0;
    border-radius: 0;
    border: 0;
}
button {
    background-color: transparent;
}
fieldset,
img {
    border: 0;
}
img {
    vertical-align: top;
}
ol,
ul {
    list-style: none;
}
address,
em {
    font-style: normal;
}
a {
    color: inherit;
    text-decoration: none;
}
a:hover {
    text-decoration: underline;
}
iframe {
    overflow: hidden;
    margin: 0;
    border: 0;
    padding: 0;
    vertical-align: top;
}
mark {
    background-color: transparent;
}
i {
    font-style: normal;
}
.blind {
    display: none;
}
.topBtn {
    position: fixed;
    bottom: 30px;
    right: 30px;
    cursor: pointer;
}
/* top  */
.topBtn.active {
    z-index: 10;
    opacity: 1;
}

.topBtn:hover,
.topBtn:focus,
.topBtn:active {
    outline: 0 none;
}

input {
    outline: none;
}
::-webkit-scrollbar {
    display: none;
}
