.homeHeader {
  -webkit-animation: fadein 0.35s;
          animation: fadein 0.35s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.homeHeader {
  width: 100%;
  height: 80px;
  padding: 12px 0;
  border-bottom: 1px solid transparent;
  box-shadow: 0 10px 10px -10px rgba(161, 161, 161, 0.485);
}

.blacklogo {
  background: url(/public/chemiverse_img/logo2.png);
  width: 161px;
  height: 41px;
  text-indent: -9999px;
}

.homeContainer {
  display: flex;
  flex-direction: row;
  margin: auto;
  justify-content: space-around;
  align-items: center;
}

.homeNav {
  width: auto;
  margin: 0;
  color: #404040;
}

.navlist {
  margin: 0 15px;
}

.homeNav a {
  color: #404040;
  text-decoration: none;
  font-size: 19px;
  font-weight: 700;
  position: relative;
}
.homeNav a::after {
  content: "";
  width: 0;
  height: 10px;
  display: inline-block;
  background: #bbc0f5;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
  transition: all 0.5s;
}
.homeNav a:hover::after {
  transition: all 0.35s;
  font-weight: 900;
  /*  border-bottom: 6px solid #6f76c173;
       box-shadow:inset 0 -6px 0 #6f76c173;
       transition: all .35s; */
  content: "";
  width: 100%;
}

.title {
  color: #6f76c1;
  font-size: 32px;
  font-weight: bold;
}

.onup_btn {
  background-color: #ffffff;
  border: 2px solid #ccc;
  border-radius: 30px;
  height: 50px;
  width: 200px;
  color: #6f76c1;
  font-weight: bold;
  position: relative;
  font-size: 15px;
}
.onup_btn:hover {
  background-color: #6f76c1;
  color: #ffffff;
  transition: all 0.3s;
  font-weight: bold;
  border: 2px solid transparent;
}
.onup_btn a:hover {
  transition: all 0.3s;
  text-decoration: none;
}

input[id=menuicon] {
  display: none;
}

.sidebar {
  display: none;
}

/* .hamburger{
  display: none;
} */
@media screen and (max-width: 720px) {
  .homeHeader {
    height: 60px;
  }
  .homeContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .homeNav {
    display: none;
  }
  .title {
    display: none;
  }
  .onup_btn {
    position: fixed;
    bottom: 0;
    width: 100vw;
    background-color: #6f76c1;
    border-radius: 0;
    color: #ffffff;
    border: 1px solid #6f76c1;
    display: none;
  }
  .blacklogo {
    width: 100px;
    height: 8vw;
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 5vw;
    z-index: 2;
  }
  input[id=menuicon] {
    display: none;
  }
  input[id=menuicon] + label {
    display: block;
    width: 30px;
    height: 30px;
    position: relative;
    cursor: pointer;
    margin-right: 5vw;
    z-index: 2;
  }
  input[id=menuicon] + label span {
    display: block;
    position: absolute;
    width: 100%;
    height: 5px;
    border-radius: 30px;
    background-color: #B1B8C1;
    transition: all 0.35s;
  }
  input[id=menuicon] + label span:nth-child(1) {
    top: 0;
  }
  input[id=menuicon] + label span:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
  }
  input[id=menuicon] + label span:nth-child(3) {
    bottom: 0;
  }
  input[id=menuicon]:checked + label span:nth-child(1) {
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
  input[id=menuicon]:checked + label span:nth-child(2) {
    opacity: 0;
  }
  input[id=menuicon]:checked + label span:nth-child(3) {
    bottom: 50%;
    transform: translateY(50%) rotate(-45deg);
  }
  .sidebar {
    display: block;
    width: 100vw;
    height: 100%;
    background-color: #fff;
    position: fixed;
    top: 62px;
    right: -100%;
    z-index: 1;
    transition: all 0.35s;
  }
  input[id=menuicon]:checked + label + div {
    right: 0;
  }
  input[id=menuicon]:checked + label + div .onup_menu {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    background-color: #6f76c1;
    border-radius: 0;
    color: #ffffff;
    border: 1px solid #6f76c1;
    height: 60px;
    transition: all 0.35s;
  }
  .navlist_white {
    color: #222;
    text-align: left;
    font-size: 4.8vw;
    height: 6vw;
    margin: 6vw auto;
    border-bottom: 1px solid #ccc;
    font-weight: bold;
    padding-left: 6vw;
    line-height: 1vw;
  }
  /* .hamburger{
    display: block;
    font-size: 10vw;
    margin-right: 5vw;
    color: #B1B8C1; 
  }*/
}/*# sourceMappingURL=HomeHeader.module.css.map */