.mainBox {
  width: 69.444vw;
  margin: 0 auto;
  position: relative;
  padding: 1vw 1vw;
  border-radius: 1vw;
  box-sizing: border-box;
  background-color: #ddd;
  .eventBox {
    width: 40vw;
    height: 20vw;
    border-radius: 1vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: flex-end;
    align-items: center;
    border: 1px solid #ddd;
    background-color: #fff;
    z-index: 10;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    padding: 3vw 0;
  }
}
.FullCalendar {
  height: 10vw;
}
@media screen and(max-width: 720px) {
  .mainBox {
    .eventBox {
      height: 50vw;
    }
  }
  .FullCalendar {
    height: 75vw;
  }
}
