/* App */
.cf::after {
    content: "";
    display: block;
    width: 0;
    height: 0;
    clear: both;
}

.buttonArea {
    float: right;
    margin-bottom: 20px;
}

/* editor */

.ThinkEditor {
    .textareaDiv {
        border: 1px solid rgb(196, 196, 196);
        border-radius: 10px;
        margin-bottom: 30px;
        &:hover {
            box-shadow: rgba(0, 0, 0, 0.15) 5px 5px 5px;
            transition: 0.3s;
        }
    }
    .textArea {
        width: 100%;
        padding: 30px;
        height: 100%;
        outline: none;
    }
    button {
        width: 80px;
        padding: 10px;
        margin: 5px;
        cursor: pointer;
        border: 2px solid #6f76c1;
        color: #6f76c1;
        border-radius: 20px;
        font-size: 0.9vw;
        font-weight: 700;
        &:hover {
            background-color: #6f76c1;
            color: #fff;
            transition: 0.4s;
        }
    }
}

/* List  */

/* ITEM */

.ThinkItem {
    margin-bottom: 10px;
    .date {
        margin-right: 10px;
        color: gray;
    }
    .info {
        border-bottom: 1px solid gray;
        padding-bottom: 10px;
        margin-bottom: 10px;
    }
    .content {
        margin-bottom: 30px;
        margin-top: 30px;
        font-weight: bold;
        border: 1px solid gray;
        padding: 30px;
        text-align: left;
        font-weight: normal;
        border: 1px solid rgb(215, 215, 215);
        border-radius: 10px;
        box-shadow: rgba(0, 0, 0, 0.095) 5px 5px 5px;
        max-height: 500px;
        textarea {
            width: 100%;
            height: 100%;
            outline: none;
            min-height: 10px;
        }
    }

    button {
        width: 85px;
        padding: 10px;
        margin: 5px;
        cursor: pointer;
        border: 2px solid #6f76c1;
        color: #6f76c1;
        border-radius: 20px;
        font-size: 0.9vw;
        font-weight: 700;
        &:hover {
            background-color: #6f76c1;
            color: #fff;
            transition: 0.4s;
        }
    }
}
